'use client';

import { useState, useEffect } from 'react';
import { useAuth } from '@/contexts/AuthContext';
import { 
  ArrowUpIcon, 
  ArrowDownIcon, 
  BanknotesIcon, 
  ClockIcon, 
  ArrowPathIcon,
  DocumentTextIcon,
  CurrencyDollarIcon
} from '@heroicons/react/24/outline';
import Link from 'next/link';
import Image from 'next/image';

enum PaymentStatus {
  PENDING = 'pending',
  PROCESSING = 'processing',
  COMPLETED = 'completed',
  FAILED = 'failed'
}

interface Transaction {
  id: string;
  type: 'earning' | 'withdrawal';
  amount: number;
  fee: number;
  netAmount: number;
  status: PaymentStatus;
  date: string;
  description: string;
  client?: {
    id: string;
    name: string;
    image?: string;
  };
  orderId?: string;
  paymentMethod?: string;
}

export default function EarningsPage() {
  const { user } = useAuth();
  const [loading, setLoading] = useState(true);
  const [activeTab, setActiveTab] = useState<'earnings' | 'withdrawals'>('earnings');
  const [timeFilter, setTimeFilter] = useState<'week' | 'month' | 'year' | 'all'>('month');
  const [transactions, setTransactions] = useState<Transaction[]>([]);
  const [filteredTransactions, setFilteredTransactions] = useState<Transaction[]>([]);
  const [stats, setStats] = useState({
    totalEarnings: 0,
    availableBalance: 0,
    pendingEarnings: 0,
    completedOrders: 0,
    averageRating: 0,
    monthlyGrowth: 0
  });
  
  useEffect(() => {
    // محاكاة جلب البيانات من API
    const fetchData = async () => {
      setLoading(true);
      
      // بيانات تجريبية
      const mockTransactions: Transaction[] = [
        {
          id: 't1',
          type: 'earning',
          amount: 150,
          fee: 15,
          netAmount: 135,
          status: PaymentStatus.COMPLETED,
          date: '2023-12-01T14:30:00Z',
          description: 'تصميم شعار شركة بالذكاء الاصطناعي',
          client: {
            id: 'client1',
            name: 'محمد العبدالله',
            image: '/images/avatars/client1.jpg'
          },
          orderId: 'ord1',
          paymentMethod: 'بطاقة ائتمان'
        },
        {
          id: 't2',
          type: 'earning',
          amount: 300,
          fee: 30,
          netAmount: 270,
          status: PaymentStatus.COMPLETED,
          date: '2023-11-28T14:15:00Z',
          description: 'كتابة محتوى لموقع شركة برمجيات',
          client: {
            id: 'client2',
            name: 'سارة الأحمد',
            image: '/images/avatars/client2.jpg'
          },
          orderId: 'ord2',
          paymentMethod: 'محفظة إلكترونية'
        },
        {
          id: 't3',
          type: 'withdrawal',
          amount: 400,
          fee: 5,
          netAmount: 395,
          status: PaymentStatus.COMPLETED,
          date: '2023-11-25T09:45:00Z',
          description: 'سحب أرباح إلى الحساب البنكي',
          paymentMethod: 'تحويل بنكي'
        },
        {
          id: 't4',
          type: 'earning',
          amount: 450,
          fee: 45,
          netAmount: 405,
          status: PaymentStatus.COMPLETED,
          date: '2023-11-20T09:45:00Z',
          description: 'تصميم قصة متحركة للأطفال',
          client: {
            id: 'client3',
            name: 'فيصل المطيري',
            image: '/images/avatars/client3.jpg'
          },
          orderId: 'ord3',
          paymentMethod: 'بطاقة ائتمان'
        },
        {
          id: 't5',
          type: 'earning',
          amount: 600,
          fee: 60,
          netAmount: 540,
          status: PaymentStatus.PENDING,
          date: '2023-11-15T11:20:00Z',
          description: 'تحليل بيانات المبيعات وإنشاء تقرير شامل',
          client: {
            id: 'client4',
            name: 'نورة الغامدي',
            image: '/images/avatars/client4.jpg'
          },
          orderId: 'ord4',
          paymentMethod: 'بطاقة ائتمان'
        },
        {
          id: 't6',
          type: 'withdrawal',
          amount: 300,
          fee: 5,
          netAmount: 295,
          status: PaymentStatus.PENDING,
          date: '2023-11-10T15:30:00Z',
          description: 'سحب أرباح إلى المحفظة الإلكترونية',
          paymentMethod: 'محفظة إلكترونية'
        },
        {
          id: 't7',
          type: 'earning',
          amount: 800,
          fee: 80,
          netAmount: 720,
          status: PaymentStatus.COMPLETED,
          date: '2023-10-25T15:30:00Z',
          description: 'تطوير روبوت محادثة لخدمة العملاء',
          client: {
            id: 'client5',
            name: 'عبدالله العتيبي',
            image: '/images/avatars/client5.jpg'
          },
          orderId: 'ord5',
          paymentMethod: 'محفظة إلكترونية'
        },
        {
          id: 't8',
          type: 'earning',
          amount: 200,
          fee: 20,
          netAmount: 180,
          status: PaymentStatus.COMPLETED,
          date: '2023-10-15T13:20:00Z',
          description: 'توليد أفكار لحملة تسويقية',
          client: {
            id: 'client6',
            name: 'ليلى الحربي',
            image: '/images/avatars/client6.jpg'
          },
          orderId: 'ord6',
          paymentMethod: 'بطاقة ائتمان'
        }
      ];
      
      const mockStats = {
        totalEarnings: 2500,
        availableBalance: 1200,
        pendingEarnings: 600,
        completedOrders: 12,
        averageRating: 4.8,
        monthlyGrowth: 15
      };
      
      setTimeout(() => {
        setTransactions(mockTransactions);
        setFilteredTransactions(mockTransactions);
        setStats(mockStats);
        setLoading(false);
      }, 1000);
    };
    
    fetchData();
  }, []);
  
  useEffect(() => {
    // تصفية المعاملات حسب نوعها والإطار الزمني
    const filterTransactions = () => {
      let result = [...transactions];
      
      // تصفية حسب نوع المعاملة
      if (activeTab === 'earnings') {
        result = result.filter(t => t.type === 'earning');
      } else {
        result = result.filter(t => t.type === 'withdrawal');
      }
      
      // تصفية حسب الإطار الزمني
      if (timeFilter !== 'all') {
        const now = new Date();
        const startDate = new Date();
        
        switch (timeFilter) {
          case 'week':
            startDate.setDate(now.getDate() - 7);
            break;
          case 'month':
            startDate.setMonth(now.getMonth() - 1);
            break;
          case 'year':
            startDate.setFullYear(now.getFullYear() - 1);
            break;
        }
        
        result = result.filter(t => new Date(t.date) >= startDate);
      }
      
      // ترتيب المعاملات من الأحدث إلى الأقدم
      result.sort((a, b) => new Date(b.date).getTime() - new Date(a.date).getTime());
      
      setFilteredTransactions(result);
    };
    
    filterTransactions();
  }, [transactions, activeTab, timeFilter]);
  
  // حالة التحميل
  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
      </div>
    );
  }
  
  // تنسيق التاريخ
  const formatDate = (dateString: string) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('ar-SA', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
    });
  };
  
  // عرض حالة المعاملة
  const getStatusBadge = (status: PaymentStatus) => {
    switch (status) {
      case PaymentStatus.PENDING:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-yellow-100 text-yellow-800">
            <ClockIcon className="h-3 w-3 ml-1" />
            قيد الانتظار
          </span>
        );
      case PaymentStatus.PROCESSING:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-blue-100 text-blue-800">
            <ArrowPathIcon className="h-3 w-3 ml-1 animate-spin" />
            قيد المعالجة
          </span>
        );
      case PaymentStatus.COMPLETED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-green-100 text-green-800">
            مكتمل
          </span>
        );
      case PaymentStatus.FAILED:
        return (
          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium bg-red-100 text-red-800">
            فشلت العملية
          </span>
        );
    }
  };
  
  return (
    <div className="space-y-6">
      <div className="bg-white shadow rounded-lg">
        <div className="px-4 py-5 sm:px-6 border-b border-gray-200">
          <h3 className="text-lg font-medium leading-6 text-gray-900">الأرباح والمدفوعات</h3>
          <p className="mt-1 text-sm text-gray-500">
            إدارة أرباحك وسجل معاملاتك المالية
          </p>
        </div>
        
        {/* نظرة عامة */}
        <div className="p-6">
          <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-6">
            <div className="bg-gradient-to-br from-indigo-500 to-indigo-600 rounded-lg p-6 text-white shadow-md">
              <div className="flex justify-between items-start">
                <div>
                  <h4 className="text-lg font-semibold mb-2">الرصيد المتاح</h4>
                  <p className="text-3xl font-bold">{stats.availableBalance} ر.س</p>
                </div>
                <div className="bg-white bg-opacity-20 p-3 rounded-full">
                  <BanknotesIcon className="h-8 w-8 text-white" />
                </div>
              </div>
              <Link
                href="/dashboard/earnings/withdraw"
                className="mt-4 inline-flex items-center px-4 py-2 bg-white text-indigo-700 font-medium rounded-md text-sm hover:bg-indigo-50 transition-colors"
              >
                سحب الأرباح
              </Link>
            </div>
            
            <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
              <div className="flex justify-between items-start">
                <div>
                  <h4 className="text-lg font-semibold text-gray-800 mb-2">إجمالي الأرباح</h4>
                  <p className="text-3xl font-bold text-gray-900">{stats.totalEarnings} ر.س</p>
                </div>
                <div className="bg-green-100 p-3 rounded-full">
                  <ArrowUpIcon className="h-8 w-8 text-green-500" />
                </div>
              </div>
              <div className="mt-4 flex items-center text-sm">
                <div className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${stats.monthlyGrowth >= 0 ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}`}>
                  {stats.monthlyGrowth >= 0 ? (
                    <ArrowUpIcon className="h-3 w-3 ml-1" />
                  ) : (
                    <ArrowDownIcon className="h-3 w-3 ml-1" />
                  )}
                  {Math.abs(stats.monthlyGrowth)}%
                </div>
                <span className="text-gray-500 mr-2">مقارنة بالشهر الماضي</span>
              </div>
            </div>
            
            <div className="bg-white rounded-lg p-6 border border-gray-200 shadow-sm">
              <div className="flex justify-between items-start">
                <div>
                  <h4 className="text-lg font-semibold text-gray-800 mb-2">أرباح قيد الانتظار</h4>
                  <p className="text-3xl font-bold text-gray-900">{stats.pendingEarnings} ر.س</p>
                </div>
                <div className="bg-yellow-100 p-3 rounded-full">
                  <ClockIcon className="h-8 w-8 text-yellow-500" />
                </div>
              </div>
              <div className="mt-4 flex items-center text-sm">
                <DocumentTextIcon className="h-4 w-4 text-gray-400 ml-1" />
                <span className="text-gray-500">{stats.completedOrders} طلب مكتمل</span>
                <div className="mx-2 h-1 w-1 rounded-full bg-gray-300"></div>
                <div className="flex items-center">
                  <div className="flex">
                    {Array.from({ length: 5 }).map((_, i) => (
                      <svg
                        key={i}
                        className={`h-4 w-4 ${i < Math.floor(stats.averageRating) ? 'text-yellow-400' : 'text-gray-300'}`}
                        fill="currentColor"
                        viewBox="0 0 20 20"
                      >
                        <path d="M9.049 2.927c.3-.921 1.603-.921 1.902 0l1.07 3.292a1 1 0 00.95.69h3.462c.969 0 1.371 1.24.588 1.81l-2.8 2.034a1 1 0 00-.364 1.118l1.07 3.292c.3.921-.755 1.688-1.54 1.118l-2.8-2.034a1 1 0 00-1.175 0l-2.8 2.034c-.784.57-1.838-.197-1.539-1.118l1.07-3.292a1 1 0 00-.364-1.118L2.98 8.72c-.783-.57-.38-1.81.588-1.81h3.461a1 1 0 00.951-.69l1.07-3.292z" />
                      </svg>
                    ))}
                  </div>
                  <span className="text-gray-500 mr-1">{stats.averageRating}</span>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      
      {/* سجل المعاملات */}
      <div className="bg-white shadow rounded-lg">
        <div className="px-4 py-5 sm:px-6 border-b border-gray-200 flex flex-wrap items-center justify-between gap-4">
          <div>
            <h3 className="text-lg font-medium leading-6 text-gray-900">سجل المعاملات</h3>
          </div>
          
          <div className="flex space-x-2 space-x-reverse">
            <div className="inline-flex rounded-md shadow-sm">
              <button
                type="button"
                onClick={() => setActiveTab('earnings')}
                className={`px-4 py-2 text-sm font-medium rounded-r-md border ${
                  activeTab === 'earnings'
                    ? 'bg-indigo-600 text-white border-indigo-600'
                    : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-50'
                }`}
              >
                الأرباح
              </button>
              <button
                type="button"
                onClick={() => setActiveTab('withdrawals')}
                className={`px-4 py-2 text-sm font-medium rounded-l-md border ${
                  activeTab === 'withdrawals'
                    ? 'bg-indigo-600 text-white border-indigo-600'
                    : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-50'
                }`}
              >
                السحوبات
              </button>
            </div>
            
            <select
              className="block appearance-none bg-white border border-gray-300 rounded-md py-2 pr-3 pl-10 text-base focus:outline-none focus:ring-indigo-500 focus:border-indigo-500"
              value={timeFilter}
              onChange={(e) => setTimeFilter(e.target.value as any)}
            >
              <option value="week">آخر أسبوع</option>
              <option value="month">آخر شهر</option>
              <option value="year">آخر سنة</option>
              <option value="all">الكل</option>
            </select>
          </div>
        </div>
        
        <div className="overflow-x-auto">
          {filteredTransactions.length === 0 ? (
            <div className="text-center py-12">
              <CurrencyDollarIcon className="h-12 w-12 text-gray-300 mx-auto" />
              <p className="mt-2 text-gray-500">لا توجد معاملات في هذه الفترة</p>
            </div>
          ) : (
            <table className="min-w-full divide-y divide-gray-200">
              <thead className="bg-gray-50">
                <tr>
                  <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    المعاملة
                  </th>
                  {activeTab === 'earnings' && (
                    <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                      العميل
                    </th>
                  )}
                  <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    المبلغ
                  </th>
                  <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    التاريخ
                  </th>
                  <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                    الحالة
                  </th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-gray-200">
                {filteredTransactions.map((transaction) => (
                  <tr key={transaction.id}>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="ml-3 flex-shrink-0">
                          {transaction.type === 'earning' ? (
                            <div className="bg-green-100 p-2 rounded-full">
                              <ArrowUpIcon className="h-5 w-5 text-green-500" />
                            </div>
                          ) : (
                            <div className="bg-blue-100 p-2 rounded-full">
                              <ArrowDownIcon className="h-5 w-5 text-blue-500" />
                            </div>
                          )}
                        </div>
                        <div>
                          <div className="text-sm font-medium text-gray-900">
                            {transaction.type === 'earning' ? 'دفعة واردة' : 'سحب أرباح'}
                          </div>
                          <div className="text-sm text-gray-500">
                            {transaction.description}
                          </div>
                          {transaction.orderId && (
                            <Link href={`/dashboard/requests/${transaction.orderId}`} className="text-xs text-indigo-600 hover:text-indigo-800">
                              رقم الطلب: {transaction.orderId}
                            </Link>
                          )}
                        </div>
                      </div>
                    </td>
                    
                    {activeTab === 'earnings' && (
                      <td className="px-6 py-4 whitespace-nowrap">
                        {transaction.client ? (
                          <div className="flex items-center">
                            <div className="flex-shrink-0 h-8 w-8 relative">
                              <Image
                                className="rounded-full object-cover"
                                src={transaction.client.image || "/images/avatar.jpg"}
                                alt={transaction.client.name}
                                fill
                                onError={(e) => {
                                  const target = e.target as HTMLImageElement;
                                  target.src = "/images/avatar.jpg";
                                }}
                              />
                            </div>
                            <div className="mr-3">
                              <div className="text-sm font-medium text-gray-900">
                                <Link href={`/providers/${transaction.client.id}`} className="hover:text-indigo-600">
                                  {transaction.client.name}
                                </Link>
                              </div>
                            </div>
                          </div>
                        ) : (
                          <span className="text-gray-500">-</span>
                        )}
                      </td>
                    )}
                    
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">{transaction.netAmount} ر.س</div>
                      <div className="text-xs text-gray-500">
                        {transaction.type === 'earning' ? (
                          <>بعد خصم العمولة: {transaction.fee} ر.س</>
                        ) : (
                          <>رسوم السحب: {transaction.fee} ر.س</>
                        )}
                      </div>
                    </td>
                    
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {formatDate(transaction.date)}
                    </td>
                    
                    <td className="px-6 py-4 whitespace-nowrap">
                      {getStatusBadge(transaction.status)}
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}
        </div>
        
        {activeTab === 'earnings' && (
          <div className="px-6 py-4 border-t border-gray-200 bg-gray-50">
            <div className="text-sm text-gray-600">
              <p>• يتم خصم عمولة المنصة بنسبة 10% من قيمة كل طلب.</p>
              <p>• الأرباح تكون متاحة للسحب بعد 14 يوم من إتمام الطلب.</p>
            </div>
          </div>
        )}
        
        {activeTab === 'withdrawals' && (
          <div className="px-6 py-4 border-t border-gray-200">
            <Link
              href="/dashboard/earnings/withdraw"
              className="inline-flex items-center px-4 py-2 border border-transparent shadow-sm text-sm font-medium rounded-md text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
            >
              طلب سحب جديد
            </Link>
          </div>
        )}
      </div>
    </div>
  );
} 